.checkboxes__inner::-webkit-scrollbar {
  width: 8px;
}
.checkboxes__inner::-webkit-scrollbar-track {
  background: #fff;
  border: 1px solid #DDDDDD;
  border-radius: 4px;
}
.checkboxes__inner::-webkit-scrollbar-thumb {
  background: #599919;
  border-radius: 4px;
}
.checkboxes__inner::-webkit-scrollbar-thumb:hover {
  background: #599919;
}

.checkboxes__item input[type=checkbox],
.checkboxes__item input[type=radio] {
  display: none;
}
.checkboxes__item input[type=checkbox] + label.checkbox,
.checkboxes__item input[type=radio] + label.radio {
  cursor: pointer;
  display: block;
  position: relative;
  min-height: 24px;
  padding-left: 36px;
  padding-top: 4px;
  color: #000;
  font-size: 18px;
  font-weight: 700;
}
.checkboxes__item input[type=radio] + label.radio:before,
.checkboxes__item input[type=checkbox] + label.checkbox:before {
  content: "";
  position: absolute;
  border: 1px solid #545454;
  height: 24px;
  width: 24px;
  left: 0px;
  top: 0px;
  transition: background 124ms linear;
}
.checkboxes__item input[type=radio] + label.radio:before {
  border-radius: 50%;
}
.checkboxes__item input[type=radio] + label.radio:after,
.checkboxes__item input[type=checkbox] + label.checkbox:after {
  display: none;
}
.checkboxes__item input[type=radio]:checked + label.radio:after,
.checkboxes__item input[type=checkbox]:checked + label.checkbox:after {
  display: block;
  content: "";
  position: absolute;
  width: 12px;
  height: 12px;
  left: 6px;
  top: 6px;
  background: #545454;
  pointer-events: none;
  cursor: default;
}
.checkboxes__item input[type=radio]:checked + label.radio:after {
  border-radius: 50%;
}
.checkboxes__item input[type=radio] + label.radio:hover:before,
.checkboxes__item input[type=checkbox] + label.checkbox:hover:before {
  background: #fff;
}
.checkboxes__item input[type=radio]:checked + label.radio:hover:before,
.checkboxes__item input[type=checkbox]:checked + label.checkbox:hover:before {
  background: transparent;
}

